$brand-bg: #D82228;
$brand-color: #fff;
$grid-float-breakpoint: 767px;

html {
    height: 100%;
}
body {
    min-height: 100%;
    min-width: 320px;
    overflow-y:scroll;
}
.navbar {
    background: $brand-bg;
    color: $brand-color;
    border: none;
}
.navbar-toggle {
    float:left;
    background: none!important;
    border:none!important;
    .icon-bar {
        background: #f2f2f2!important;
    }
}

.navbar-login {
    padding-top: 10px;
    @media (max-width: $grid-float-breakpoint) {
        padding-right: 40px;
        text-align: center;
    }    
    .brand {
        vertical-align: -webkit-baseline-middle;
        @media (max-width: $grid-float-breakpoint) {
            display: none;
        }
    }
    .exit {
        vertical-align: -webkit-baseline-middle;
        float: right;
        color: $brand-color;
        @media (max-width: $grid-float-breakpoint) {
            display: none;
        }
    }
}

.index-main {
    padding-top: 65px;
    @media (max-width: $grid-float-breakpoint) {
        &>.container {padding: 0;}
    }        
}

.main-container {
    @media (max-width: $grid-float-breakpoint) {
        padding: 0;
    }    
}


.aside-left {
    position: relative;
    background: #fff;
    @media (max-width: $grid-float-breakpoint) {
        position: fixed;
        top: 45px;
        left: 0;
        bottom: 0;
        padding: 0;
        width: 70%;
        min-width: 300px;
        z-index: 100;
        left: -110%;
        /* left: 0;
        -webkit-transform: translate(-110%, 0);
                transform: translate(-110%, 0); */
        background: #f2f2f2;
        box-shadow: 2px 5px 6px #888;
        -webkit-overflow-scrolling:touch;
        overflow: auto;
        overflow-x: hidden;
        @at-root .nav-show &.aside-left-transition{
            /* -webkit-transform: translate(0);
                    transform: translate(0); */
            left: 0;
        }
        @at-root .nav-show {
            overflow-y: hidden;
        }
    }    
    .aside-left-modal-mask {
        display: none;
        position: absolute;
        z-index: 1;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: #000;
        opacity: 0.3;
        filter: alpha(opacity=30);
    }
    @at-root .modal-open & .aside-left-modal-mask {
        display: block;
    }
}

.modal-open {
    overflow: auto;
    background: #b2b2b2;
    .aside-left-modal-mask {
        display: block;
    }
    .help-icon {
        display: none!important;
    }
    .navbar {
        opacity: 0.5;
        filter: opacity(alpha=50);
    }
}


.aside-left-transition {
    -webkit-transition: all 0.3s linear;
            transition: all 0.3s linear;
}

.nav-mask {
    display: none;
    position: fixed;
    z-index: 99;
    top: 45px; 
    bottom: 0;
    left: 0;
    right: 0;
    background: #000;
    opacity: .2;
    filter: alpha(opacity=20);
    @media (max-width: $grid-float-breakpoint) {
        @at-root .nav-show & {display: block;}
    }
}

.list-group-item {
    padding-left: 50px;
    color: $brand-bg;
    cursor: pointer;
    background: url(/w20e/common/img/sidebar_active.png) #f5f5f5 no-repeat;
    -webkit-transition: all ease 0.3s;
    -moz-transition: all ease 0.3s;
    -o-transition: all ease 0.3s;
    transition: all ease 0.3s;
    background-position-x: 20px;
    
    &.collapsed {
		color: #333;
        background-image: url(/w20e/common/img/sidebar.png);
    }
    &.network {
        background-position:20px -2px;
    }
    &.wifi {
        background-position:20px -34px;
    }
    &.acManage {
        background-position:20px -379px;
    }
    &.behavior {
        background-position:20px -66px;
    }
    &.qos {
        background-position:20px -98px;
    }
    &.vpn {
        background-position:20px -130px;
    }
    &.security {
        background-position:20px -162px;
    }
    &.authServer {
        background-position:20px -194px;
    }
    &.pppoeAuth {
        background-position:20px -226px;
    }
    &.cloudAuth {
        background-position:20px -260px;
    }
    &.weixin {
        background-position:20px -292px;
    }
    &.virtualServer {
        background-position:20px -324px;
    }
    &.USB {
        background-position:20px -356px;
    }
    &.sysManage {
        background-position:20px -388px;
    }
    &.sysStatus {
        background-position:20px -420px;
    }
    
    @media (max-width: $grid-float-breakpoint) {
        border-left: none;
        border-right: none;
        &:first-child {
            border-top: none;
        }
    }
}
.list-group {
    min-width: 160px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    border-bottom: 1px solid #CCC;
    border-bottom: 1px solid rgba(0, 0, 0, 0.15);
    @media (max-width: $grid-float-breakpoint) {
        .menu-group {
            border-left: none;
            border-right: none;            
        }
    }
}
.menu-group {
    z-index: 100;
    min-width: 160px;
    padding: 0;
    margin: 0;
    list-style: none;
    font-size: 14px;
    text-align: left;
    background-color: #FFF;
    border: 1px solid #CCC;
    border: 1px solid rgba(0, 0, 0, 0.15);
    border-bottom: none;
    -o-background-clip: padding-box;
    background-clip: padding-box;

    a {
        display: block;
        padding: 8px;
        padding-left:50px;
        clear: both;
        font-weight: normal;
        font-size: 13px;
        line-height: 1.42857143;
        color: #333;
        white-space: nowrap;
        margin-left: -1px;
        margin-right: -1px;
    }
    a.active {
        background: #D82228;
        color: #FFF;
        
    }
    &#qos {
        display: none;
    }
}
.msgbox {
    position: fixed;
    top: 140px;
    left: 50%;
    width:320px;
    margin-left: -160px;
    padding: 28px 18px;
    color: #fff;
    font-size: 14px;
    z-index: 9999;
    text-align: center;
    -webkit-border-radius: 5px;
    border-radius: 5px;
    background-color: #666;
    opacity: .9;
    filter: alpha(opacity=90);
    &.ani-init {
        transition: all 0.1s ease-in-out;
        transform: translateY(20px);
        opacity: 0;
    }
    &.ani-final {
        transform:none;
        opacity: .9;
    }
}


.index-footer {
    padding: 10px 15px;
    border-top: 1px dashed #999;
    .copyright {
        text-align: center;
        color: #888;
    }
}

.progress p {
    position:absolute;
    text-align:center;
    width:100%;
    color:black;
}

