::-webkit-scrollbar-track-piece {
    background: #ddd;
}
::-webkit-scrollbar {
    width: 10px;
    height: 10px;
}
::-webkit-scrollbar-thumb {
    background: #bbb;
    border-radius: 5px;
}

.copyright a:hover{
    color: #ac1b20;
}
.copyright a{
    color: #999;
}

.help-icon {
    display: inline-block;
    position: absolute;
    z-index: 1;
    top: 6px; 
    right: 8px;
    width: 30px;
    height: 30px;
    padding: 0;
    overflow: hidden;
    text-align: center;
    line-height: 30px;
    font-style: normal;
    border:1px solid #ccc;
    color: #999;
    border-radius: 4px;
    cursor: pointer;
    &>span {
        position: relative;
        top: 0;
        -webkit-transition: all .3s;
                transition: all .3s;
    }
    &:hover>span {
        top: -30px;
    }
}

#helpBtn>span>span {
    color: $brand-bg;
}


